:export{mediumScreen:992px;smallScreen:540px}button{background-color:#222;padding:0.9rem 3.7rem;border:none;outline:none;border-radius:23px;font-family:"montserrat";cursor:pointer}@media (max-width: 540px){button{padding:0.9rem 1rem;width:85%;max-width:18rem}}button>span{display:inline-block;color:#fff;font-size:1.125rem;font-weight:600;line-height:1.25rem}button:hover{background-color:#e65540}button:active{box-shadow:-3px 3px 14px 0px rgba(0,0,0,0.2)}button.secondary{background-color:#fff;margin:auto}button.secondary *{color:#21125e}button.secondary:hover{color:#e65540}button.secondary:hover *{color:#e65540}button:disabled{background-color:#c4c4c4}button:disabled,button:disabled:hover{cursor:default}
