:export{mediumScreen:992px;smallScreen:540px}@media (max-width: 992px){.checkout{overflow-x:hidden}}.checkout__menu__bar{text-align:center;border-bottom:1px solid #f6f6f6;padding:1rem 0}.checkout__menu svg{width:8rem}.checkout__menu a{text-transform:uppercase;text-decoration:none;color:#c4c4c4;font-weight:600;margin:1rem;display:block}@media (min-width: 540px){.checkout__menu a{position:absolute;top:1rem;left:1rem}}.checkout__menu a:hover{color:#7d7d7d}.checkout__step{margin-bottom:1rem;display:flex;align-items:center}.checkout__step span{float:left;border:2px solid #21125e;border-radius:1rem;width:2rem;height:2rem;padding:0.3rem;text-align:center;font-weight:600;color:#21125e;margin-right:1rem}.checkout__step--inactive span{color:#fff;background-color:#21125e}.checkout__content{margin:0 0 3rem 3rem}.checkout__content button[type="submit"]{margin:2.5rem 0}@media (max-width: 992px){.checkout__content button[type="submit"]{display:block;width:calc(100% - 3rem);margin:2.5rem auto;max-width:inherit}}@media (max-width: 992px){.checkout__content{margin-left:0}}.checkout__header{text-transform:uppercase;font-weight:600;font-size:1.5rem}.checkout__grid{display:flex;justify-content:space-between;margin:1.5rem 0;align-items:flex-start}.checkout__grid__content{max-width:32rem;width:32rem}@media (max-width: 992px){.checkout__grid__content{margin:0 auto}}.checkout__grid--review{display:block}
