@import url("~reset-css");@import url("~typeface-montserrat");:export{mediumScreen:992px;smallScreen:540px}.u-lead{font-size:1.5rem;font-weight:600;line-height:normal;padding-bottom:10px}.u-lead--bold{color:#7d7d7d;font-weight:800}.u-uppercase{text-transform:uppercase}body{font-family:"montserrat";font-size:1rem;line-height:1.25rem;color:#323232}body *{box-sizing:border-box}body h1{font-size:4rem;line-height:1}body h3{font-size:1.5rem;line-height:1.7rem}body h4{font-size:1.125rem}body a{text-decoration:none;color:inherit}body p{line-height:1.5rem}body #root{display:flex;min-height:100vh;flex-direction:column}body #root>div:first-of-type{flex:1}body .container{width:1140px;max-width:100vw;margin:0 auto;padding:0 1rem}@media (max-width: 992px){body .container{width:100%}}body table{width:100%;margin:3rem 0;font-size:.875rem}body table th{color:#7d7d7d;text-align:left;padding:1rem .5rem;font-weight:600}body table tr{border-bottom:1px solid #f6f6f6}body table td{padding:1rem .5rem;vertical-align:middle}body table td img{vertical-align:middle;margin-right:1rem}body .checkbox{position:relative;display:inline}body .checkbox:before{content:"";display:block;position:absolute;left:0.4rem;top:0.05rem;width:0.3rem;height:0.6rem;border:solid #fff;border-width:0 2px 2px 0;transform:rotate(45deg);z-index:1}body .checkbox input{display:none}body .checkbox input:checked ~ span:before{background-color:#21125e}body .checkbox span{margin-left:2rem}body .checkbox span:before{background-color:#fff;border-radius:0.2rem;border:1px solid #21125e;content:"";display:block;height:1rem;position:absolute;top:0;width:1rem}.form-error{color:#c22d74}
